
.app-login {
  width: 100%;
  height: 100%;
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr=#00a597, endColorstr=#FFFFFFFF, GradientType=0 );
  background-color: #00a597;
  background: linear-gradient(to bottom, #00a597 45%, #fff 45%, #fff 100%);
  position: relative;
}
.app-login .login-wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    width: 650px;
    height: 500px;
    background: #fff;
    border: 1px solid #ccc;
    padding: 0 30px;
}
.app-login .login-wrapper h1 {
      height: 73px;
      line-height: 72px;
      text-align: center;
      font-size: 30px;
}
.app-login .login-wrapper .login-content {
      margin: 20px 95px 0;
      border-bottom: 1px solid #ccc;
      overflow: hidden;
}
.app-login .login-wrapper .login-content input {
        display: block;
        width: 100%;
        margin-top: 30px;
        background: #f8f8f8;
}
.app-login .login-wrapper .submit {
      display: block;
      height: 45px;
      text-align: center;
      line-height: 45px;
      border-radius: 6px;
      background: #00a597;
      color: #fff;
      font-size: 16px;
      margin: 30px 0;
}



