
@charset "UTF-8";
body,
div,
header,
footer,
nav,
section,
aside,
article,
ul,
dd,
li,
a,
p,
dl,
dt,
h1,
h2,
h3,
h4,
h5,
h6,
i,
b,
textarea,
button,
input,
select,
figure,
figcaption {
  padding: 0;
  margin: 0;
  list-style: none;
  font-style: normal;
  text-decoration: none;
  border: none;
  color: #333;
  box-sizing: border-box;
  font-family: pingguo, "Microsoft Yahei", sans-serif;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
}
body:focus,
  div:focus,
  header:focus,
  footer:focus,
  nav:focus,
  section:focus,
  aside:focus,
  article:focus,
  ul:focus,
  dd:focus,
  li:focus,
  a:focus,
  p:focus,
  dl:focus,
  dt:focus,
  h1:focus,
  h2:focus,
  h3:focus,
  h4:focus,
  h5:focus,
  h6:focus,
  i:focus,
  b:focus,
  textarea:focus,
  button:focus,
  input:focus,
  select:focus,
  figure:focus,
  figcaption:focus {
    outline: none;
}

/*定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  background-color: #F5F5F5;
}

/*定义滚动条轨道 内阴影+圆角*/
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 1px transparent;
  border-radius: 10px;
  background-color: #F5F5F5;
}

/*定义滑块 内阴影+圆角*/
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #555;
}
input[type="button"],
input[type="submit"],
input[type="search"],
input[type="reset"] {
  -webkit-appearance: none;
}
textarea {
  -webkit-appearance: none;
}
html,
body {
  height: 100%;
  width: 100%;
}

/* 页面标题 */
.page-title {
  font-size: 18px;
  height: 50px;
  line-height: 50px;
  padding-left: 30px;
  border-bottom: 1px solid #ccc;
}

/* 修改element-ui样式 */
/* $--color-light:red; */
.el-tooltip__popper.is-dark span {
  color: #fff;
}
.wq-sidebar {
  height: 100%;
  overflow: hidden;
}
.wq-sidebar .sidebar-fold {
    cursor: pointer;
    text-align: center;
    height: 28px;
}
.wq-sidebar .sidebar-fold .fold-svg {
      color: #333;
      font-size: 26px;
}
.wq-sidebar .sidebar-fold.hide-sidebar {
      width: 64px;
}
.wq-sidebar.show-sidebar {
    min-width: 200px;
}
.wq-sidebar.hide-sidebar {
    min-width: 64px;
}



